import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'home',
      component: resolve => require(['@/views/Home'], resolve),
      meta: {
        nav: "Home",
        show: true,
        isActive: '1',
        type:0
      }
    },
    {
      path: '/art',
      name: 'Art',
      component: resolve => require(['@/views/article'], resolve),
      meta: {
        nav: "Article",
        show: true,
        isActive:'2',
        type:1
      }
    },
    {
      path: '/artDetail',
      name: 'ArtDetail',
      component: resolve => require(['@/views/article_detail'], resolve),
      meta: {
        nav: "详情",
        show: true,
        isActive:'2',
        type:2,
        parent:'art',
        parentName:'Article'
      }
    },
    {
      path: '/aboutMe',
      name: 'AboutMe',
      component: resolve => require(['@/views/about_me'], resolve),
      meta: {
        nav: "AboutMe",
        show: true,
        isActive:'6',
        type:1
      }
    }
  ]
})
