<template>
    <div class="nav_box">
        <div class="nav_box_pc">
            <div>JyLiu</div>
            <ul class="nav_box_pc_menu">
                <li :class="[menuItemClass,{activeClass: isActive === item.id}]" v-for="item in menuList" :key="item.id" @click="toPath(item)">{{item.title}}</li>
            </ul>
        </div>
        <div class="nav_box_phone" v-if="navShow === true">
            <div class="nav_box_phone_icon"> <a @click="goBack" style="color: white;font-size: 18px;margin-left: 5px;" v-if="nav!=='Home'">《</a></div>
            <div class="nav_box_phone_name">{{nav}}</div>
            <div class="nav_box_phone_menu" @click="showMenu">
                <div class="nav_box_phone_menu_btn"></div>
            </div>
        </div>
        <div v-if="flag" :class="[show ? 'nav_box_phoneMenu' : 'nav_box_phoneMenuOut']">
            <div class="nav_box_phoneMenu_back"></div>
            <ul class="nav_box_phoneMenu_ul">
                <li :class="[phoneMenuItemClass,{activeClass: isActive === item.id}]" v-for="item in menuList" :key="item.id" @click="toPath(item)">{{item.title}}</li>
            </ul>
        </div>
    </div>
</template>

<script>

    export default {
        watch: {
           "$route"(val) {
                this.nav = val.meta.nav;
                this.navShow = val.meta.show;
                this.isActive = val.meta.isActive;
           }
        },
        data(){
          return {
              show:false,
              flag:false,
              nav:'',
              navShow:null,
              isActive:'1',
              activeClass:'active',
              menuItemClass:'nav_box_pc_menu_item',
              phoneMenuItemClass:'nav_box_phoneMenu_ul_li',
              menuList:[
                  {id:'1', title:'HOME',ref:'/'},
                  {id:'2', title:'Article',ref:'art'},
                  {id:'3', title:'NAV3',ref:''},
                  {id:'4', title:'NAV4',ref:''},
                  {id:'5', title:'NAV5',ref:''},
                  {id:'6', title:'ABOUT',ref:'aboutMe'}
              ]
          }
        },
        created(){
            this.nav = this.$route.meta.nav;
            this.navShow = this.$route.meta.show;
            this.isActive = this.$route.isActive;
        },
        mounted() {
        },
        methods:{
            toPath(val){
                this.isActive = val.id;
                this.$router.push({path:val.ref});
                this.show = !this.show;
            },
            showMenu(){
                this.flag= true;
                this.show = !this.show;
            },
            goBack(){
                this.$router.go(-1);
            }
        }
    }
</script>
<style lang="scss">
    $prefix: nav_box;

    .#{$prefix}{
        &_pc{
            height: 60px;
            color: #ffffff;
            background-color: cadetblue;
            display: flex;
            justify-content: space-between;
            align-items: center;
            padding: 0 60px;
            &_menu{
                list-style: none;
                display: flex;
                height: 100%;
                align-items: center;
                &_item{
                    height: 100%;
                    padding: 0 15px;
                    display: flex;
                    cursor: pointer;
                    align-items: center;
                }
                &_item:hover{
                    background-color: deepskyblue;
                    opacity: 0.5;
                }
                .activeClass{
                    background-color: deepskyblue;
                    opacity: 0.8;
                }
            }
        }
        &_phone{
            background-color: cadetblue;
            height: 50px;
            display: flex;
            align-items: center;
            padding-right: 10px;
            z-index: 3;
            &_icon{
                width: 33.33%;
            }
            &_name{
                width: 44.44%;
                color: white;
                font-size: 1.3rem;
                text-align: center;
            }
            &_menu{
                width: 33.33%;
                text-align: right;
                &_btn{
                    display: inline-table;
                    width: 30px;
                    height: 40px;
                    background-color: white;
                }
            }
        }
        &_phoneMenu{
            width: 100%;
            height: calc(100vh - 50px);
            //height: 100%;
            position: fixed;
            z-index: 2;
            top: 50px;
            animation-name: move_in;
            animation-duration: 1s;
            animation-iteration-count : 1;
            animation-fill-mode : forwards;
            overflow-y: auto;
            &_back{
                width: 100%;
                height: 100%;
                background-color: #000000;
                opacity: 0.3;
            }
            &_ul{
                width: 100%;
                position: absolute;
                color: #ffffff;
                top: 0;
                &_li{
                    font-size: 1.5rem;
                    list-style: none;
                    padding: 20px;
                    border-bottom: 1px solid #f4f4f4;
                    text-align: center;
                }
                .activeClass{
                    background-color: deepskyblue;
                }
            }
        }
        &_phoneMenuOut {
            width: 100%;
            height: calc(100vh - 50px);
            position: fixed;
            z-index: 2;
            top: 50px;
            animation-name: move_out;
            animation-duration: 1s;
            animation-iteration-count: 1;
            animation-fill-mode: forwards;
        }
    }
    @keyframes move_in {
        from{
            transform: translateX(-100%);
            opacity: 0;
        }
        to{
            transform: translateX(0px);
            opacity: 1;
        }
    }
    @keyframes move_out {
        from{
            transform: translateX(0);
            opacity: 1;
        }
        to{
            transform: translateX(-101%);
            opacity: 0;
        }
    }
</style>
